import React, { Component } from 'react'
import './Cart.css';
import minus from '.././images/minus.png';
import coat from '.././images/coat.png';
import dress from '.././images/dress.png';
import scarf from '.././images/scarf.png';


export default class Cart extends Component {
    constructor(props) {
        super(props)

        this.state = {
            index: props.index
        }
    }

    render() {
        return (
            <div>
                {(() => {
                    if (this.props.index===0) {
                        return (
                            <img src={coat} alt="cart" className="inline pic" />
                        )
                    } else if (this.props.index===1) {
                        return (
                            <img src={dress} alt="cart" className="inline pic" />
                        )
                    } else {
                        return (
                            <img src={scarf} alt="cart" className="inline pic" />
                        )
                    }
                })()}
                <p className="inline" style={{margin:'10px'}}><b>{this.props.name}</b> :</p>
                <p className="inline" style={{margin:'10px'}}>Price = {this.props.price} <small>NIS</small></p>
                <p className="inline" style={{margin:'10px'}}>Amount = {this.props.amount}</p>
                <button title="Delete From Cart" className='inline' style={{margin:'10px'}}
                 onClick={()=>{this.props.delete(this.props.name)}}>
                     <img src={minus} alt="plus" className="inline img" />
                </button>
            </div>
            
        )
    }
}