import './App.css';
import React from 'react';
import Product from './Components/Product';
import Cart from './Components/Cart';
import cart from './images/cart.PNG';
import home from './images/home.png';
import Background from './images/Background.jpg';
import girl from './images/girl.png';
import girlChoose from './images/girlChoose.png';


export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [
        { name: 'Coat', price: 200 },
        { name: 'Dress', price: 100 },
        { name: 'Scarf', price: 20 }
      ],
      goToCart: false,
      goHome: false,
      sum: 0,
      proInCart: 0,
      cart: [
        {
          name: 'Coat',
          price: 0, amount: 0
        },
        {
          name: 'Dress',
          price: 0, amount: 0
        },
        {
          name: 'Scarf',
          price: 0, amount: 0
        }
      ]
    };
    this.goToCart = this.goToCart.bind(this);
    this.goHome = this.goHome.bind(this);

  }

  style = {
    backgroundImage: `url(${Background})`,
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    height: '100vh'
  }

  goToCart() {
    this.setState({
      goToCart: !this.state.goToCart,
    });
  }
  goHome() {
    this.setState({
      goHome: !this.state.goHome,
    });
  }

  addTocart = (n, p) => {
    // 1. Make a shallow copy of the cart
    let newCart = [...this.state.cart];
    if (n === 'Coat') {
      // 2. Make a shallow copy of the item you want to mutate
      let item = { ...newCart[0] };
      // 3. Replace the properties you're intested in
      item.price = item.price + p;
      item.amount = item.amount + 1;
      // 4. Put it back into our array. N.B. we *are* mutating the array here, but that's why we made a copy first
      newCart[0] = item;
    }
    else if (n === 'Dress') {
      let item = { ...newCart[1] };
      item.price = item.price + p;
      item.amount = item.amount + 1;
      newCart[1] = item;
    }
    else {
      let item = { ...newCart[2] };
      item.price = item.price + p;
      item.amount = item.amount + 1;
      newCart[2] = item;
    }
    // 5. Set the state to our new copy
    this.setState({
      cart: newCart
    })
    let sum = this.state.sum
    sum = newCart[0].price + newCart[1].price + newCart[2].price
    this.setState({
      sum: sum
    })

    let proInCart = this.state.proInCart
    proInCart++
    this.setState({
      proInCart: proInCart
    })
  }

  deleteFromCart = (n) => {
    let newCart = [...this.state.cart];
    if (n === 'Coat') {
      let item = { ...newCart[0] };
      item.price = item.price - 200;
      if (item.amount === 0) {
        return null;
      }
      else {
        item.amount = item.amount - 1;
      }
      newCart[0] = item;
    }
    else if (n === 'Dress') {
      let item = { ...newCart[1] };
      item.price = item.price - 100;
      if (item.amount === 0) {
        return null;
      }
      else {
        item.amount = item.amount - 1;
      }
      newCart[1] = item;
    }
    else {
      let item = { ...newCart[2] };
      item.price = item.price - 20;
      if (item.amount === 0) {
        return null;
      }
      else {
        item.amount = item.amount - 1;
      }
      newCart[2] = item;
    }
    this.setState({
      cart: newCart
    })
    let sum = this.state.sum
    sum = newCart[0].price + newCart[1].price + newCart[2].price
    this.setState({
      sum: sum
    })
    let proInCart = this.state.proInCart
    proInCart--
    this.setState({
      proInCart: proInCart
    })
  }

  emptyCart = () => {
    let newCart = [...this.state.cart];
    let item1 = { ...newCart[0] };
    let item2 = { ...newCart[1] };
    let item3 = { ...newCart[2] };
    if (item1.amount === 0 && item2.amount === 0 && item3.amount === 0) {
      alert('Cart is empty...');
    }
    else {
      item1.price = 0;
      item1.amount = 0;
      newCart[0] = item1;
      item2.price = 0;
      item2.amount = 0;
      newCart[1] = item2;
      item3.price = 0;
      item3.amount = 0;
      newCart[2] = item3;
      this.setState({
        cart: newCart
      })
      let sum = this.state.sum
      sum = 0;
      this.setState({
        sum: sum
      })
      let proInCart = 0;
      this.setState({
        proInCart: proInCart
      })
      alert('Purchase completed successfully, thank you!');
    }

  }

  render() {
    return (
      <div style={this.style}> {(this.state.goToCart && this.state.goHome === false) || (this.state.goHome && this.state.goToCart === false) ?
        <div className="App">
          <button className="alignleft" title="Go Home" onClick={this.goHome}><img src={home} alt="home" className="inline imgBtn" /></button>
          <h1 className="inline" style={{ marginRight: '10px', marginLeft: '-40px' }}>Cart&nbsp;</h1>
          <img src={cart} alt="cart" className="inline imgCart" />
          {this.state.cart.map((element, i) => {
            return <Cart delete={this.deleteFromCart} name={element.name} price={element.price} amount={element.amount} index={i} />
          })}
          <p className="inline" style={{ marginRight: '30px' }}><b>Total = {this.state.sum} <small>NIS</small></b></p>
          <button onClick={this.emptyCart}><p id='buyBtn' title='Click To Buy'><b>Buy</b></p></button><br />
          <img src={girl} alt="girl" className="imgGirl" />
        </div>
        :
        <div className="App">
          <button className="alignleft" title="Go Home"><img src={home} alt="home" className="inline imgBtn" /></button>
          <h1 className="inline">Lital-Shop Bla</h1>
          <p className="inline alignright inCart"><b>{this.state.proInCart}</b></p>
          <button className="alignright" title="Go To Cart" onClick={this.goToCart}><img src={cart} alt="cart" className="inline imgBtn" /></button>
          {this.state.products.map((element, i) => {
            return <Product cart={this.addTocart} name={element.name} price={element.price} index={i} />
          })}
          <br />
          <img src={girlChoose} alt="girlChoose" className="imgGirl" />
        </div>
      }</div>

    );
  }
}
